// CONVERTING PX TO REM
@function rem($size, $default-size: $default-font-size) {
	@return ($size / $default-size) + rem; }

// Dark / Light mode
@mixin dark {
	body.dark {
		@content; } }

// Responsive breakpoints
@mixin r($width) {
	@media only screen and (max-width: $width+ "px") {
		@content; } }

@mixin rmin($width) {
	@media only screen and (min-width: $width+ "px") {
		@content; } }

@mixin xxl {
	@include r(1400) {
		@content; } }

@mixin xl {
	@include r(1200) {
		@content; } }

@mixin lg {
	@include r(992) {
		@content; } }

@mixin md {
	@include r(790) {
		@content; } }
@mixin md-650 {
	@include r(650) {
		@content; } }

@mixin sm {
	@include r(576) {
		@content; } }
